import React from 'react'
import { StyleSheet, Text, SafeAreaView, View } from 'react-native'
import { VictoryTheme, VictoryPie, VictoryLabel } from '../../modules/Victory'
import Svg, { Circle } from 'react-native-svg'

const randomColor = () =>
  ('#' + ((Math.random() * 0xffffff) << 0).toString(16) + '000000').slice(0, 7)

const pieSize = 400
const innerRadius = pieSize / 10

const FoodWheel = () => {
  return (
    <SafeAreaView style={styles.container}>
      <Svg width={pieSize} height={pieSize}>
        <Circle
          {...props.svg.circle}
          onPress={() => {
            alert('onPress circle')
          }}
          onStartShouldSetResponder={() => true}
          onResponderTerminationRequest={() => true}
          onResponderMove={null}
          onResponderTerminate={null}
          onResponderRelease={null}
          onResponderGrant={null}
        />
        <VictoryLabel {...props.victory.label} />
        <VictoryPie
          {...props.victory.pie}
          data={[
            { x: 'Cats', y: 5 },
            { x: 'Dogs', y: 40 },
            { x: 'Birds', y: 55 },
          ]}
          events={[
            {
              childName: 'all',
              target: 'data',
              eventHandlers: {
                onClick: () => {
                  return [
                    {
                      target: 'data',
                      mutation: ({ style }) => {
                        return style.fill === '#c43a31'
                          ? null
                          : { style: { fill: '#c43a31' } }
                      },
                    },
                    {
                      target: 'labels',
                      mutation: ({ text }) => {
                        return text === 'clicked' ? null : { text: 'clicked' }
                      },
                    },
                  ]
                },
              },
            },
          ]}
        />
      </Svg>
    </SafeAreaView>
  )
}

const props = {
  svg: {
    circle: {
      cx: pieSize / 2,
      cy: pieSize / 2,
      r: innerRadius,
      fill: 'white',
    },
  },
  victory: {
    label: {
      textAnchor: 'middle',
      style: { fontSize: 20 },
      text: 'Quay',
      x: pieSize / 2,
      y: pieSize / 2,
      text: 'Quay',
    },
    pie: {
      standalone: false,
      width: pieSize,
      height: pieSize,
      startAngle: 90,
      endAngle: 450,
      labelRadius: ({ innerRadius }) => innerRadius * 2.5,
      innerRadius: innerRadius + 5,
      padAngle: 1,
      theme: VictoryTheme.material,
    },
  },
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
})

export default FoodWheel
